import React, { Component } from 'react';
import './ButtonComponent.css';





class ButtonComponent extends Component {
      

   render() {
     return (
      
		<div className="ButtonBlock">

                  <button onClick={this.props.showEditComponentChanger}>Edit </button>

                  <button onClick={this.props.showStyleComponentChange}>Style</button>

                  <button disabled={!this.props.enable} onClick={this.props.safer}>Safe</button>

                  <button disabled={!this.props.enable} onClick={this.props.showAddComponentChange}>Add </button>

                  <button>Block</button>

            </div>
      );}
}

export default ButtonComponent;