import React, {  Component } from 'react';
import './App.css';


import TitleComponent from './TitleComponent.js';
import DisplayComponent from './DisplayComponent.js';
import ButtonComponent from './ButtonComponent.js';
import EditComponent from './EditComponent.js';
import StyleComponent from './StyleComponent.js';
import AddComponent from './AddComponent.js';


  

class App extends Component {
 constructor(props){
	super(props);
	this.state = {
		par: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum" ,
		textarea: '',
    fontSize: '',
    fontName: 'Times new roman',
    fontBold: 'normal',
    fontItalic: 'normal',
    fontUnderline: 'none',
    showStyleComponent: false,
    showEditComponent: false,
    showAddComponent: false,
    enable: false,
    showAddTable: true,
    showAddList: false,
    addListType: true,
    row:'',
    cell: ' ',
    cellWidth: ' ',
    cellHeight: ' ',
    borderWidth: ' ',
    borderStyle: 'solid',
    borderColor: 'black'
    }
  }


  changer(e){
    this.setState({
      par: e.target.value,
      textarea: this.state.par,
    });
  }

  safer(){
    this.setState({
      textarea: '',
      showEditComponent: false,
      showStyleComponent: false,
      showAddComponent: false
    });
  }
	fontSizeChange(e){
    this.setState({
      fontSize: e.target.value
    });
	}

  fontFamilyChange(e){
   this.setState({
    fontName: e.target.value
   });
  }
  
  fontBoldChange(){
    if(this.state.fontBold === "normal"){
      this.setState({ fontBold: "bold" });
    }else{
      this.setState({ fontBold: "normal" });
    }
  }
  fontItalicChange(){
    if(this.state.fontItalic === "normal"){
      this.setState({ fontItalic: "italic" });
    }else{
      this.setState({ fontItalic: "normal" });
    }
  }

  fontUnderlineChange(){
    if(this.state.fontUnderline === "none"){
      this.setState({ fontUnderline: "underline" });
    }else{
      this.setState({ fontUnderline: "none" });
    }
  }

  showStyleComponentChange(){
    this.setState({
      showStyleComponent: true,
      showEditComponent: false,
      showAddComponent: false
  });
  }

  showAddComponentChange(){
    this.setState({
      showStyleComponent: false,
      showEditComponent: false,
      showAddComponent: true
  });
  }
  showEditComponentChanger(){
    this.setState({  
      showStyleComponent: false,
      showEditComponent: true,
      showAddComponent: false,
      textarea: this.state.par,
      enable: true
    });
  }

  showAddTableChange(){
    this.setState({
      showAddTable: true,
      showAddList: false
    });
  }
  showAddListChange(){
    this.setState({
      showAddTable: false,
      showAddList: true
    });
  }
  addListTypeUl(){
    this.setState({
      addListType: true
      });
  }
  addListTypeOl(){
    this.setState({
      addListType: false
      });
  }
newRow(e) {
  this.setState({
    row: e.target.value
  });
}
newCell(e) {
  this.setState({
    cell: e.target.value
  });
}
newCellWidth(e){
  this.setState({
    cellWidth: e.target.value
  });
}
newCellHeight(e){
  this.setState({
    cellHeight: e.target.value
  });
}
newBorderWidth(e){
  this.setState({
    borderWidth: e.target.value
  });
}
newBorderStyle(e){
  this.setState({
   borderStyle: e.target.value
  });
 }
 newBorderColor(e){
  this.setState({
   borderColor: e.target.value
  });
 }

 createTable(){
  this.setState({
    showEditComponent: true,
    showAddComponent: false,
  //   newTextarea: `${this.state.textarea} <table border="${this.state.borderWidth}" style=border-style:"${this.state.borderStyle}" bordercolor="${this.state.borderColor}">`
  // });
  // this.setState({ textarea: this.state.newTextarea});
  //   for(let i=0; i<this.state.row; i++){      
  //     this.setState({newTextarea:
  //        `${this.state.textarea}  <tr>`
  //       });
  //       this.setState({ textarea: this.state.newTextarea});
  //        for(var j=0; j<this.state.cell; j++){
  //         this.setState({newTextarea: `${this.state.textarea}  <td style=border-style:"${this.state.borderStyle}" width="${this.state.cellWidth}" height="${this.state.cellHeight}"></td>`   
  //        });
  //        this.setState({ textarea: this.state.newTextarea});
  //        this.setState({ newTextarea: `${this.state.textarea} </tr></table>` 
  //   });
    // console.log(newTextarea);
  // }
 });}



   render() {
     return (
      <div className="TextEditor">

            <TitleComponent />
        
            <DisplayComponent
                par = {this.state.par}
                fontSize = {this.state.fontSize}
                fontName = {this.state.fontName}
                fontBold = {this.state.fontBold}
                fontItalic = {this.state.fontItalic}
                fontUnderline = {this.state.fontUnderline}
                
            />

            <ButtonComponent
              showEditComponentChanger = {this.showEditComponentChanger.bind(this)}
              safer = {this.safer.bind(this)}
              showStyleComponentChange = {this.showStyleComponentChange.bind(this)}
              showAddComponentChange = {this.showAddComponentChange.bind(this)}
              enable = {this.state.enable}
            />

            {this.state.showStyleComponent ? <StyleComponent
                fontSize = {this.state.fontSize}
                fontSizeChange = {this.fontSizeChange.bind(this)}
                fontName = {this.state.fontName}
                fontFamilyChange = {this.fontFamilyChange.bind(this)}  
                fontBold = {this.state.fontBold}  
                fontBoldChange = {this.fontBoldChange.bind(this)} 
                fontItalic = {this.state.fontItalic}  
                fontItalicChange = {this.fontItalicChange.bind(this)} 
                fontUnderline = {this.state.fontUnderline}
                fontUnderlineChange = {this.fontUnderlineChange.bind(this)} 

                  /> : null}
                

                {this.state.showEditComponent ? <EditComponent 
                    textarea = {this.state.textarea}
                    changer = {this.changer.bind(this)}
                    row = {this.state.row}
                    cell = {this.state.cell}
                    cellWidth = {this.state.cellWidth}
                    cellHeight = {this.state.cellHeight}
                    borderWidth = {this.state.borderWidth}
                    borderStyle = {this.state.borderStyle}
                    borderColor = {this.state.borderColor}
                    /> : null}
                  

                {this.state.showAddComponent ? <AddComponent
                showAddTableChange = {this.showAddTableChange.bind(this)}
                showAddTable = {this.state.showAddTable}
                showAddListChange = {this.showAddListChange.bind(this)}
                showAddList = {this.state.showAddList}
                addListType = {this.state.addListType}
                addListTypeUl = {this.addListTypeUl.bind(this)}
                addListTypeOl = {this.addListTypeOl.bind(this)}
                row = {this.state.row}
                newRow = {this.newRow.bind(this)}
                cell = {this.state.cell}
                newCell = {this.newCell.bind(this)}
                cellWidth = {this.state.cellWidth}
                newCellWidth = {this.newCellWidth.bind(this)}
                cellHeight = {this.state.cellHeight}
                newCellHeight = {this.newCellHeight.bind(this)}
                borderWidth = {this.state.borderWidth}
                newBorderWidth = {this.newBorderWidth.bind(this)}
                borderStyle = {this.state.borderStyle}
                newBorderStyle = {this.newBorderStyle.bind(this)}
                borderColor = {this.state.borderColor}
                newBorderColor = {this.newBorderColor.bind(this)}
                createTable = {this.createTable.bind(this)}
                
                   /> : null}

      </div>
      
    );
  }
}

export default App;

