import React, { Component } from 'react';
import './AddComponent.css';

class AddComponent extends Component {
   render() {
     return (
			<div className="AddBlock">
          <h1>Choose what to add:</h1>

						 <label>
							 		Table
										<input type="radio"
													name="addTabOrList" 
													value="table"
													onClick={this.props.showAddTableChange}
													/>
          	</label> 

           	<label>
						 			List
										<input type="radio" 
													name="addTabOrList"
													value="list" 
													onClick={this.props.showAddListChange}
													/>
          	</label> 

					{this.props.showAddTable ? 
						<div className="tableForm">

							<form className="rowCell">
									<label>Input count of row:
										<input type="text"
													 id="row"
													 name="row"
													 value={this.props.row}
													 onChange={this.props.newRow} 
													 />
									</label>

									<label>Input count of cell:
										<input type="text"
													 id="cell"
													 name="cell"
													 value={this.props.cell}
													 onChange={this.props.newCell} 
													 />
									</label>
							</form>

							<form>
									<h2>Table style:</h2>

									<label>
											Input width of cell:
										<input type="text"
													 id="cellWidth"
													 name="cellWidth" 
													 value={this.props.cellWidth}
													 onChange={this.props.newCellWidth}
													 />
									</label>
									
									<label>
											Input height of cell:
										<input type="text"
													 id="cellHeight"
													 name="cellHeight"
													 value={this.props.cellHeight}
													 onChange={this.props.newCellHeight} 
													 />
									</label>
							</form>

							<form>
									<label>
											Border width:
										<input type="text"
													 id="borderWidth"
													 name="borderWidth"
													 value={this.props.borderWidth}
													 onChange={this.props.newBorderWidth} />
									</label>

									<label>Border style:
										<select id="brdrStyle" onChange={this.props.newBorderStyle}>
												<option value="solid">solid</option>
												<option value="dotted">dotted</option>
												<option value="dashed">dashed</option>
												<option value="double">double</option>
										</select>
									</label>

									<label>Border color:
										<select id="brdrClr" onChange={this.props.newBorderColor}>
												<option value="black">black</option>
												<option value="red">red</option>
												<option value="blue">blue</option>
										</select>
									</label>
							</form>

							<button id="createTableBtn" onClick={this.props.createTable}>Create</button>
						
							</div> 
						: null}

      			{this.props.showAddList ? 
							<div className="listForm">
								<form>
										<h2>Choose type of list:</h2>

										<label>
											Unordered list:
												<input type="radio"
															id="ul"
															name="typeOfList"
															value="ul"
															onClick={this.props.addListTypeUl} 
															/>
										</label>

										<label>
											Ordered list:
												<input type="radio"
															id="ol"
															name="typeOfList"
															value="ol"
															onClick={this.props.addListTypeOl} 
															/>
										</label>
								</form>

									{this.props.addListType ? 
										<form id="ulType">
											<label>Type of mark:
												<select id="ultype">
													<option value="circle">circle</option>
													<option value="disc">disc</option>
													<option value="square">square</option>
												</select>
											</label>
										</form> 
									: null }

								  {this.props.addListType ? null : 
										<form id="olType">
											<label>Type of mark:
												<select id="oltype">
													<option value="1">1 2 3</option>
													<option value="A">A B C</option>
													<option value="a">a b c</option>
												</select>
											</label>
										</form> 
									}

									<form>
										<label>Input count of list items:
											<input type="text" id="countItems" name="countItems" />
										</label>
									</form>

									<button id="createListBtn">Create</button>
								</div> 
						 : null}

    </div>    
	);
 }
}

export default AddComponent;